*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.header{display:flex;height:auto;justify-content:flex-start;margin-bottom:1em;margin-top:2em;padding-left:2em;display:flex;align-items:center;border-bottom:1px solid #b53d2f}.header__img{width:4em;margin-right:1em}@media screen and (min-width: 768px){.header__img{width:7em}}.header__title{font-size:2em;margin-right:25px;color:#b53d2f}@media screen and (min-width: 768px){.header__title{font-size:3em}}@media screen and (min-width: 1200px){.header__title{font-size:4em}}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.page{background-color:#ffdb7b;display:flex;flex-direction:column;text-align:center;justify-content:center;flex-wrap:wrap;display:flex;flex-direction:column}.page__wrapper{width:15em;margin:auto}@media screen and (min-width: 768px){.page__wrapper{width:30em}}@media screen and (min-width: 1200px){.page__wrapper{width:35em}}.page__title{color:#b53d2f;margin-bottom:1em;font-size:1em}@media screen and (min-width: 768px){.page__title{font-size:2em;margin-top:1em}}@media screen and (min-width: 1200px){.page__title{font-size:2.7em}}.page__paragraph{color:#0b0100;margin-bottom:0.9em;font-size:0.8em}@media screen and (min-width: 768px){.page__paragraph{font-size:1em}}@media screen and (min-width: 1200px){.page__paragraph{font-size:1.5em}}.page__buttons{display:flex;justify-content:center}.page__button{background-color:#ffa649;border-radius:30%;padding:1em;border:1px solid #b53d2f;color:#b53d2f;margin-top:20px;margin-bottom:20px;margin-right:1em;font-weight:bolder;font-size:0.7em}@media screen and (min-width: 768px){.page__button{font-size:0.8em;padding:1em}}.page__button:hover{background-color:#ff8d3d;-webkit-transform:scale(1.05);transform:scale(1.05)}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.footer{width:100%;display:flex;justify-content:space-between;align-items:center;border-top:1px solid #b53d2f;margin-top:1.5em;margin-bottom:1.5em;font-size:1.3em}@media screen and (min-width: 768px){.footer{font-size:1.6em}}.footer__small{color:#b53d2f;margin:20px}.footer__logo{height:50px;margin:20px;margin-top:20px}@media screen and (min-width: 768px){.footer__logo{height:100px;margin-top:30px}}@-webkit-keyframes beats{from{-webkit-transform:none;transform:none}50%{-webkit-transform:scale(1.4);transform:scale(1.4)}to{-webkit-transform:none;transform:none}}@keyframes beats{from{-webkit-transform:none;transform:none}50%{-webkit-transform:scale(1.4);transform:scale(1.4)}to{-webkit-transform:none;transform:none}}.footer__heart{display:inline-block;font-size:20px;-webkit-animation:beats 0.5s infinite;animation:beats 0.5s infinite;color:#ff8d3d}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.form{color:#0b0100;flex-direction:column;margin-top:1em}.form__label{color:#b53d2f;background-color:#ffdb7b}@media screen and (min-width: 768px){.form__label{font-size:1.5em}}.form__input{width:250px;height:30px;color:#ff8d3d;border:1px solid #b53d2f;border-radius:6px;text-align:center;font-size:0.9em}@media screen and (min-width: 768px){.form__input{width:250px;height:40px}}.form__button{background-color:#ffa649;border-radius:30%;padding:0.7em;border:1px solid #b53d2f;color:#b53d2f;font-weight:bolder;width:7em;font-size:0.6em}@media screen and (min-width: 768px){.form__button{font-size:0.8em;width:7em}}.form__button:hover{background-color:#ff8d3d;-webkit-transform:scale(1.05);transform:scale(1.05)}.form__icon{background-color:#ff8d3d;border-radius:50%;padding:0.4rem;border:1px solid #b53d2f;color:#b53d2f}@media screen and (min-width: 768px){.form__icon{position:fixed;margin-top:-5em;margin-left:10em;padding:0.5rem}}.form__icon:hover{background-color:#b53d2f}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.container{height:18.5em;padding:0.5em;width:20.5em}.user{box-sizing:border-box;display:flex;flex-direction:column;border-radius:25px;box-shadow:5px 5px 10px #b53d2f;border:3px solid #ffa649;width:19em}.user:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.user__img{grid-row:3/4;border-radius:25px;margin:auto;height:7em;width:7em;margin-top:0.5em}.user__name{grid-row:1/2;margin:20px 25px 0 25px;color:#b53d2f;padding:3px 0 5px 0;font-size:1.25rem}.user__info{grid-row:4/5;margin:15px 15px;color:#b53d2f;text-transform:capitalize}.user__button{width:10em;border-radius:1em;margin-bottom:0.6em;background-color:#ff8d3d;color:#ffdb7b}.user__button:hover{background-color:#ff8d3d;color:#ffa649}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none;background-color:#ffdb7b}:root{font-size:16px;font-size:"Open Sans",sans-serif}.cards{display:grid;grid-column:1fr;grid-column-gap:0.63rem;-webkit-column-gap:0.63rem;column-gap:0.63rem;grid-row-gap:0.63rem;row-gap:0.63rem;margin-top:1em;justify-content:center}@media screen and (min-width: 768px){.cards{display:flex;flex-wrap:wrap;justify-content:center}}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

